import React, { useState } from "react";
import { Button, Row, Col, Table, Input } from "reactstrap";
import PropTypes from "prop-types";
import { format } from 'date-fns';
import { connect } from "react-redux";
import CheckPermission from "../../../Components/HOC/CheckPermission";

const TaxList = ({
    taxCode,
    totalCount,
    setEditOffer,
    setDeleteOffer,
    currentPage,
    itemsPerPage,
    onPageChange,
    onItemsPerPageChange,
    userPermissions
}) => {
    const totalPages = Math.ceil(totalCount / itemsPerPage);
    return (
        <div className="p-4 bg-light rounded shadow-sm">
            <Row className="mb-3 align-items-center">
                <Col xs="auto">
                    <Input
                        type="select"
                        value={itemsPerPage}
                        className=""
                        onChange={(e) => onItemsPerPageChange(Number(e.target.value))}
                        style={{ width: "120px" }}
                    >
                        {[5, 10, 25, 50].map((size) => (
                            <option key={size} value={size}>
                                {size}
                            </option>
                        ))}
                    </Input>
                </Col>
                <Col className="text-end text-muted">
                    Page {currentPage} of  {totalPages}
                </Col>
            </Row>

            <Table bordered hover className="bg-white">
                <thead className="table-primary text-center">
                    <tr>
                        <th>Tax Name</th>
                        <th>Tax Value</th>
                        <th>Tax Type</th>
                        <th>Start Date</th>
                        <th>End Date</th>
                        <th>Status</th>
                        <th>Visible To All</th>
                        <CheckPermission accessRolePermission={userPermissions} permission="edit">
                        <th>Actions</th>
                        </CheckPermission>
                    </tr>
                </thead>
                <tbody>
                    {taxCode && Array.isArray(taxCode) && taxCode.map((obj) => {
                        return (
                            <tr key={obj?._id}>
                                <td className="text-capitalize fw-semibold text-primary">
                                    {obj?.taxName}
                                </td>
                                <td>{`${obj?.taxValue}${obj?.taxType === 'FIXED' ? '₹' : '%'}`}</td>
                                <td>{obj?.taxType}</td>
                                <td>{format(new Date(obj?.startDate), 'dd MMM, yyyy')}</td>
                                <td>{format(new Date(obj?.endDate), 'dd MMM, yyyy')}</td>
                                <td>{obj?.status ? 'Active' : 'Inactive'}</td>
                                <td>{obj?.visibleToAll ? 'Yes' : 'No'}</td>
                                <td>
                                     <CheckPermission
                                      accessRolePermission={userPermissions}
                                      permission="edit"
                                      >
                                    <Button
                                        size="sm"
                                        color="info"
                                        className="me-2"
                                        onClick={() => setEditOffer({ isOpen: true, data: obj })}
                                        >
                                        <i className="ri-quill-pen-line"></i>
                                    </Button>
                                        </CheckPermission>
                                     <CheckPermission accessRolePermission={userPermissions}
                    permission="delete"
                >
                    
                                    <Button
                                        size="sm"
                                        color="danger"
                                        outline
                                        onClick={() =>
                                            setDeleteOffer({ isOpen: true, data: obj })
                                        }
                                        >
                                        <i className="ri-close-circle-line"></i>
                                    </Button>
                                        </CheckPermission>
                                </td>
                            </tr>
                        )
                    })}
                </tbody>
            </Table>

            <Row className="mt-4 justify-content-between align-items-center">
                <Col xs="auto">
                    <Button
                        color="secondary"
                        disabled={currentPage === 1}
                        onClick={() => onPageChange(currentPage - 1)}
                    >
                        ← Previous
                    </Button>
                </Col>
                <Col className="text-center text-muted">
                    Showing {Math.min((currentPage - 1) * itemsPerPage + 1, totalCount)}–
                    {Math.min(currentPage * itemsPerPage, totalCount)} of {totalCount}
                </Col>
                <Col xs="auto">
                    <Button
                        color="secondary"
                        disabled={currentPage === totalPages}
                        onClick={() => onPageChange(currentPage + 1)}
                    >
                        Next →
                    </Button>
                </Col>
            </Row>
        </div>
    );
};

TaxList.propTypes = {
    totalCount: PropTypes.number.isRequired,
    setDeleteOffer: PropTypes.func.isRequired,
    setEditOffer: PropTypes.func.isRequired,
    currentPage: PropTypes.number.isRequired,
    itemsPerPage: PropTypes.number.isRequired,
    onPageChange: PropTypes.func.isRequired,
    onItemsPerPageChange: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
    data: state.Taxes.data,
    totalCount: state.Taxes.totalCount,
});

export default connect(mapStateToProps)(TaxList);
